/* || ###MAIN CSS FILE */
/* || @use RULES */
/* NB: In addition to these classes, check if there are any other vanilla css or bootstrap css files included in this project */
/* || ##IMPORTS */
@import url("https://fonts.googleapis.com/css2?family=Familjen+Grotesk:ital@0;1&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik+Moonrocks&display=swap");
/* || ##RESETS */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

input,
button,
textarea {
  font: inherit;
}

a {
  text-decoration: none;
}

/* || ##VARIABLES */
/* ##COLORS */
/* ##FONTS */
/* #TEXT */
/* #HEADINGS */
/* #LINKS */
/* ##MIXINS */
/* || ##GENERAL STYLES */
html {
  scroll-behavior: smooth;
  text-align: center;
}

section {
  margin-bottom: 4em;
  scroll-margin-top: 16vh;
}

article {
  margin: 2em 0;
}

/* || ##UTILITY CLASSES */
/* #TEXT */
.text-highlight--primary {
  color: rgb(35, 187, 207);
}

.text-highlight--secondary {
  color: teal;
  font-style: italic;
}

.text-highlight--tertiary {
  color: brown;
  font-style: italic;
}

.text-shadow--tertiary {
  font-size: larger;
  color: teal;
  text-shadow: 3px 3px 3px brown;
}

.text-center {
  text-align: center;
}

.text-justify {
  text-align: justify;
}

.no-wrap {
  white-space: nowrap;
}

.first-character {
  float: left;
  font-family: Georgia, serif;
  font-size: 320%;
  line-height: 90%;
  padding-top: 1px;
  padding-right: 1px;
  padding-left: 1px;
  color: rgb(35, 187, 207);
}

