/* #LINKS */
.link {
  text-decoration: none;
  color: unset;
  font-size: medium;
  font-family: "Familjen Grotesk", sans-serif;
  padding: 0px;
}

.link:hover {
  color: teal;
  font-weight: bolder;
}

.link-highlight {
  color: rgb(35, 187, 207);
}

/* #ICONS */
.icon-highlight--primary {
  color: rgb(35, 187, 207);
}

.icon-highlight--primary:hover {
  color: teal;
  font-weight: bolder;
}

.icon-highlight--tertiary {
  color: brown;
}

.box--center {
  display: block;
  margin: auto;
}

/* #MARGINS */
.margin--sm {
  margin: 0.7em auto 2em;
}

/* || ##SITE NAVIGATION BAR */
.primary-nav {
  position: fixed;
  left: 50%;
  right: 50%;
  top: 5vh;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  scroll-margin-top: 16vh;
}

.primary-nav__nav {
  font-weight: bold;
  font-size: clamp(1em, 3.5vw, 1.8em);
}

.primary-nav__ul {
  padding: 0.2em 2.5%;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  gap: 1em;
  width: max-content;
  background-color: rgba(0, 128, 128, 0.2);
  margin-right: 1em;
}

.primary-nav__a:hover {
  color: teal;
  text-decoration: underline;
}

/* || ##FOOTER */
/* || #D#EVELOPMENT STAGE PRETESTS */
