/* #TESTING CLASSES */
.yellow {
  background-color: yellow;
}

.red {
  background-color: red;
}

/* Ensure the body takes up the full height of the viewport */
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  max-height: 50vh;
}

.qr-generator {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  border-radius: 5px;
  background-color: #eee;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: auto;
  min-height: 95vh;
  /* The following styles are in relation to the footer */
  display: flex;
  flex-direction: column;
  flex: 1;
  padding-bottom: 8.9rem;
  /* To compensate for the height of the footer and thus avoid overlap with the fixed footer*/
  position: relative;
  /* The footer is positioned absolutely; see the .qr-generator__footer style rules for details */
}

.qr-generator__header {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  background-color: #f5f5f5;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  width: 100%;
}

.qr-generator__header__icon {
  font-size: 2rem;
  color: #4caf50;
  margin-top: 0.5rem;
}

.qr-generator__header__title {
  font-size: 1.5rem;
  color: #333;
  margin: 0;
  line-height: 1;
  display: flex;
}

.qr-generator__form {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.qr-generator__form__fieldset {
  border: none;
  padding: 0;
}

.qr-generator__form__label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.qr-generator__form__input {
  padding: 0.75rem 1rem;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 1rem;
  width: 100%;
}

.qr-generator__form__input:focus {
  outline: none;
  border-color: #888;
}

.qr-generator__form__button {
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 3px;
  background-color: #4CAF50;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 1rem;
  transition: background-color 0.2s ease-in-out;
}

.qr-generator__form__button:hover {
  background-color: #3e8e41;
}

.qr-generator__form__button[disabled] {
  background-color: #ddd;
  cursor: default;
}

.qr-generator__qr-container {
  margin-top: 2rem;
  text-align: center;
}

.qr-generator__qr-container__figcaption {
  margin-top: 0.5rem;
  font-size: 0.8rem;
  color: #666;
}

.qr-generator__qr-container__figcaption__label {
  color: #999;
}

.qr-generator__download__button {
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 3px;
  background-color: #4CAF50;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 1rem;
  transition: background-color 0.2s ease-in-out;
}

.qr-generator__download__button[disabled] {
  background-color: #ddd;
  cursor: default;
}

.qr-generator__footer {
  padding: 1rem;
  text-align: center;
  border-top: 1px solid #dee2e6;
  position: absolute;
  /* the footer container is positioned relatively see the .qr-generator style rules for details */
  bottom: 0;
  width: 100%;
}

.qr-generator__footer__text {
  color: #6c757d;
  font-size: 0.875rem;
}

/* || ##MEDIA QUERIES */
/* Small screens (e.g., smartphones) */
@media (max-width: 768px) {
  .qr-generator {
    padding: 1rem;
    padding-bottom: 8.9rem;
    /* To compensate for the height of the footer and thus avoid overlap with the fixed footer*/
  }
  .qr-generator__form {
    margin-top: 1rem;
  }
  .qr-generator__qr-container {
    margin-top: 1rem;
  }
}
/* Medium screens (e.g., tablets) */
@media (min-width: 769px) and (max-width: 1024px) {
  .qr-generator {
    padding: 2rem;
    padding-bottom: 8.9rem;
    /* To compensate for the height of the footer and thus avoid overlap with the fixed footer*/
  }
  .qr-generator__qr-container {
    margin-top: 2rem;
  }
}
/* Large screens (e.g., desktops) */
@media (min-width: 1025px) {
  .qr-generator {
    padding: 3rem;
    padding-bottom: 8.9rem; /* To compensate for the height of the footer and thus avoid overlap with the fixed footer*/
  }
  .qr-generator__qr-container {
    margin-top: 3rem;
  }
}
